.App {
  position: absolute;
  right: 10vw;
  top: 8vh;
  color: white;
  max-height: 80vh;
}

.Buttons {
  overflow-y: auto;
  overflow-x: hidden;
  scrollbar-width: none;
  height: 80vh;
}

.Buttons::-webkit-scrollbar {
  display: none;
}

.Title {
  text-align: center;
  z-index: 1;
}

.MenuButton {
  width: 25vw;
  min-width: 240px;
  font-size: 15px;
  margin-top: 8px;
  background-color: rgba(29, 32, 37, 0.9);
  padding: 6px;
  border-radius: 5px;

  display: flex;
  flex-wrap: wrap;
  align-items: center;

  border: 2px solid rgb(23 32 49 / 25%);
}

.MenuButton:hover {
  background-color: rgb(20, 22, 26)
}

.MenuButtonTitle {
  font-size: 17px;
}

.MenuButtonDescription {
  font-size: 14px;
  line-break: auto;
  padding-top: 5px;
}

.MenuButtonArrow {
  position: absolute;

  right: 5%;
  font-size: 24px;;
}

.TextContainer {
  width: 93%;
}

.ArrowContainer {
  width: 7%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
}